import Vue from 'vue'
import App from './App'
import router from './router'
import Vuex from 'vuex'

Vue.use(Vuex)
Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  components: {
  	 App ,
  	
  },
  
  template: '<App/>'
})



